@-webkit-keyframes resize {
  from {
    transform: scale(0.92);
  }

  to {
    transform: scale(1.06);
  }
}

@keyframes run {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(-100%, 0);
  }
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.button {
  display: block;
  font-size: 24px;
  line-height: 30px;
  padding: 10px 40px;
  border-radius: 10px;
  background: #fff;
  transition: 0.2s background;
  box-shadow: 0px 0px 21px rgba(255, 255, 255, 0.7);
}

.button_alt {
  background: linear-gradient(180deg, #6ca1c8 0%, #427e9a 100%);
}

.button_alt .button__text {
  color: #fff;
}

.button__text {
  color: #000;
}

.link {
  text-decoration: none;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html {
  scroll-behavior: smooth;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after {
  content: "";
  content: none;
}

q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

button {
  outline: none;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

.page {
  position: relative;
  margin: 0;
  padding: 0;
}

.page_theme {
  font: 14px / 2.14 "Montserrat", Arial, sans-serif;
  color: #000;
  background: #fff;
  min-width: 100%;
  min-height: 100vh;
}

.layout {
  width: 100%;
  height: 100%;
}

.layout__wrap {
  max-width: 1440px;
  width: 100%;
  min-height: 100%;
  margin: auto;
  background: url("../img/bg_main.png") 0 0 no-repeat;
  box-sizing: border-box;
  overflow: hidden;
}

.header {
  min-height: 100vh;
  position: relative;
  z-index: 1;
}

.header__inner {
  padding: 30px 0;
  text-align: center;
}

.header__logo {
  display: block;
  margin: auto;
  width: 600px;
  height: 85px;
  margin-bottom: 160px;
}

.header__text {
  font-size: 142px;
  line-height: 140px;
  font-weight: bold;
  background: -webkit-linear-gradient(#ffe402, #ff0404);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  position: relative;
  /*  font-size: 72px;
    background-image: -webkit-linear-gradient(#eee, #333);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    position: relative; */
  /*&:after {      
       background: none;
      content: attr(data-text);
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      position: absolute;
      text-shadow: 5px 5px 5px red;

      z-index: -1; 
    }*/
}

.header__text:after {
  width: 100%;
  content: attr(data-text);
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.8);
  top: 0;
  z-index: -1;
}

.header__date {
  font-size: 80px;
  color: #fff;
  line-height: 80px;
  padding: 20px;
}

.header__go {
  width: 300px;
  margin: auto;
}

.header__scroll {
  display: flex;
  justify-content: center;
  padding-top: 60px;
}

.header__scrollLink {
  display: flex;
  justify-content: center;
}

.logo {
  width: 100%;
}

.logo__link {
  display: block;
}

.logo__img {
  margin: auto;
}

.screen__inner {
  height: 100vh;
  min-height: 600px;
}

.screen__first {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.screen__first:before {
  position: absolute;
  background: url("../img/bg__screen_first.png") 0 0 no-repeat;
  content: "";
  margin: auto;
  height: 100%;
  width: 1440px;
  background-size: cover;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.screen__first:after {
  position: absolute;
  content: "";
  background: rgba(11, 30, 36, 0.67);
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}

.screen__second {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.screen__second:before {
  position: absolute;
  background: url("../img/bg__screen_second.png") 0 0 no-repeat;
  content: "";
  margin: auto;
  height: 100%;
  width: 1440px;
  background-size: cover;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.screen__logo {
  position: relative;
  z-index: 1;
  display: flex;
  padding: 15px 10px;
}

.screen__logo_second {
  padding: 50px 10px;
}

.screen__slogan-logo {
  position: relative;
  z-index: 1;
  color: #fff;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  padding: 0 10px;
}

.screen__slogan-logo-dark {
  position: relative;
  z-index: 1;
  color: #639bc3;
  font-size: 36px;
  font-weight: bold;
  text-align: center;
  padding: 0 10px;
}

.screen__main-box {
  position: relative;
  z-index: 1;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 10px;
}

.screen__slogan-main {
  color: #fff;
  text-transform: uppercase;
  font-size: 30px;
  line-height: 38px;
  text-align: center;
}

.screen__button {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  margin-bottom: 60px;
  padding: 0 10px;
}

.screen__button-move {
  animation: resize 1s ease-in-out infinite alternate;
}

.screen__run-string {
  position: relative;
  z-index: 1;
  margin-top: auto;
  margin-bottom: 0;
}

.screen__utp {
  position: relative;
  z-index: 1;
  flex-grow: 1;
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.screen__contact {
  position: relative;
  z-index: 1;
  margin-right: 160px;
  margin-bottom: 20px;
}

.discount__inner {
  position: relative;
  z-index: 1;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.discount__slogan {
  font-size: 24px;
  margin-bottom: 20px;
}

.discount__box {
  display: flex;
  font-size: 72px;
  line-height: 72px;
  text-transform: uppercase;
  font-weight: bold;
  text-shadow: 0px 0px 21px rgba(255, 255, 255, 0.7);
}

.discount__up-to {
  font-size: 18px;
  line-height: 18px;
  padding: 16px 10px 0;
  font-weight: normal;
  position: relative;
}

.discount__up-to::after {
  content: "";
  position: absolute;
  height: 7px;
  width: 20px;
  background: #fff;
  left: 14px;
  top: 40px;
}

.discount__count {
  animation: resize 1s ease-in-out infinite alternate;
}

.run-string__inner {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  background: linear-gradient(180deg, #6ca1c8 0%, #427e9a 100%);
}

.run-string__text {
  display: inline-block;
  padding: 10px 15px;
  animation: run 1525s infinite linear;
  color: #fff;
  font-size: 24px;
  text-transform: uppercase;
}

.utp__inner {
  display: flex;
  justify-content: center;
}

.utp__item {
  width: 380px;
  display: flex;
  flex-direction: column;
  padding: 0 15px;
}

.utp__title {
  color: #639bc3;
  font-size: 28px;
  line-height: 32px;
  margin-bottom: 15px;
}

.contact__inner {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.contact__box {
  text-align: right;
}

.contact__title {
  font-size: 20px;
  line-height: 24px;
  color: #639bc3;
  font-weight: bold;
}

.contact__number {
  font-size: 20px;
  line-height: 24px;
}

html,
body {
  width: 100%;
  min-height: 100vh;
}

@media screen and (max-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 24px;
  }

  .header__logo {
    width: 500px;
    margin-bottom: 60px;
  }

  .logo__img {
    width: 100px;
  }

  .screen__logo_second {
    padding: 30px 10px;
  }

  .screen__slogan-logo {
    font-size: 14px;
  }

  .screen__slogan-logo-dark {
    font-size: 21px;
    line-height: 24px;
    margin-bottom: 50px;
  }

  .screen__slogan-main {
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 20px;
  }

  .screen__contact {
    margin-right: 0;
  }

  .discount__slogan {
    font-size: 15px;
    line-height: 15px;
  }

  .discount__box {
    font-size: 37px;
    line-height: 37px;
  }

  .discount__up-to {
    padding: 5px 7px 0;
    font-size: 12px;
    line-height: 12px;
  }

  .discount__up-to::after {
    top: 20px;
    height: 5px;
    width: 18px;
    left: 8px;
  }

  .run-string__text {
    font-size: 18px;
  }

  .utp__inner {
    flex-direction: column;
    align-items: center;
  }

  .utp__item {
    width: 100%;
    margin-bottom: 30px;
  }

  .utp__title {
    font-size: 20px;
    line-height: 34px;
  }

  .contact__inner {
    justify-content: center;
  }

  .contact__box {
    text-align: center;
  }
}

@media screen and (max-width: 640px) {
  .header__logo {
    width: 300px;
    margin-bottom: 30px;
  }

  .header__text {
    font-size: 80px;
    line-height: 80px;
  }

  .header__date {
    font-size: 40px;
  }

  .header__go {
    padding-top: 100px;
  }

  .header__scroll {
    padding-top: 40px;
  }

  .header__scrollImg {
    width: 70%;
  }
}

@media screen and (max-width: 540px) {
  .header__logo {
    font-size: 60px;
    margin-bottom: 10px;
  }

  .header__text {
    font-size: 60px;
    line-height: 60px;
  }

  .header__go {
    padding-top: 15px;
    width: 200px;
  }
}

@media screen and (max-width: 375px) {
  .screen__inner {
    height: 100%;
    min-height: 100vh;
  }

  .screen__first {
    height: 100%;
    min-height: 100vh;
  }

  .screen__first:before {
    background: url("../img/bg__screen_first_mob.png") 0 0 no-repeat;
    width: 375px;
  }

  .screen__second:before {
    background: url("../img/bg__screen_second_mob.png") 0 0 no-repeat;
    width: 375px;
  }
}